.mobile-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.mobile-modal-content {
  background: white;
  border-radius: 20px;
  padding: 20px;
  padding-left: 30px;
  width: 80%;
  max-width: 400px;
  text-align: center;
  max-height: 605px; /* Limita la altura máxima */
  overflow-y: auto; /* Activa el scroll vertical cuando excede la altura */
}
.mobile-modal-content::-webkit-scrollbar {
  overflow: hidden;
}

.mobile-modal-closeBtn {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-modal-list,
.mobile-submenu-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mobile-modal-item,
.mobile-submenu-item {
  padding: 15px;
  font-size: 18px;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
}

.mobile-modal-item.active {
  background-color: var(--main-color); /* Reemplaza con el color principal */
  color: white;
}

.mobile-submenu-title {
  margin-bottom: 10px;
}

.mobile-submenu-item:hover {
  background-color: #f0f0f0;
}

ul {
  list-style: disc;
  padding: 20;
  cursor: normal;
}
.mobile-submenu-item {
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 15px;
}
.mobile-submenu-option-item {
  font-weight: normal;
  text-align: start;
}
.mobile-submenu-option-item:hover {
  color: var(--main-color);
}

.active {
  background-color: #f0f0f0;
}
